var Fragment = require('prosemirror-model').Fragment;

function inTable(state) {
  var path = state.selection.$head.path;

  for (var i = 0; i < path.length; i += 3) {
    if (path[i].type.name === 'table') {
      return true;
    }
  }

  return false;
}

function generateEmptyCell(state, type) {
  return type.create(undefined, Fragment.fromArray([state.schema.nodes.paragraph.create()]));
}

function insertTable(state, dispatch) {
  var tr = state.tr.replaceSelectionWith(state.schema.nodes.table.create(undefined, Fragment.fromArray([state.schema.nodes.table_row.create(undefined, Fragment.fromArray([generateEmptyCell(state, state.schema.nodes.table_header), generateEmptyCell(state, state.schema.nodes.table_header)])), state.schema.nodes.table_row.create(undefined, Fragment.fromArray([generateEmptyCell(state, state.schema.nodes.table_cell), generateEmptyCell(state, state.schema.nodes.table_cell)]))])));

  if (dispatch) {
    dispatch(tr);
  }

  return true;
}

export function CreateTableTool(schema) {
  return {
    name: "table_create",
    label: "Create Table",
    displayIcon: React.createElement(TableChart, null),
    apply: insertTable,
    isEnabled: function isEnabled(state) {
      return canInsert(state, schema.nodes.table);
    },
    isVisible: function isVisible(state) {
      return !inTable(state);
    }
  };
}
